import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class MensagensService {

  url_sendMensagem = 'http://10.1.1.124:9091/mensagem';

  constructor(private http: HttpClient) { }

  sendMensagem(mensagem: string, contextos: any[]): Observable<Resposta> {
    return this.http.post<Resposta>(this.url_sendMensagem,
      { mensagem, contextos },
      { withCredentials: true, });
  }
}

export interface Mensagem {
  mensagem: string;
  contextos: [];
}

export interface Resposta {
  mensagem: string[];
  contextos: [];
}
